.card-item {
  width: fit-content;
  height: fit-content;
  position: relative;
  border: 2px solid #f9f9f987;
  transition: all 1s;
  margin-bottom: 2.4rem;
}

.card-item .card-wrapper {
  position: relative;
  color: white;
  font-family: 'Roboto', sans-serif;
}

.card-item .card-wrapper .card {
  padding: 1.6rem 1.2rem;
}

.card-item .card-wrapper .card .title {
  color: #FFF;
  padding-bottom: 0.8rem;
  border-bottom: 1px solid #eee;
  margin-bottom: 0.8rem;
}

.card-item .card-wrapper .card p {
  color: #FFF;
  line-height: 2.2rem;
}

.card-item .line {
  background: white;
  position: absolute;
  transition: none;
  transform: scale(0);
  opacity: 1;
}

.card-item .line:nth-child(1) {
  transform-origin: 100% 100%;
  height: 3px;
  width: 20%;
  bottom: 0;
}

.card-item .line:nth-child(4) {
  transform-origin: 0 100%;
  height: 3px;
  width: 20%;
  top: -1px;
  right: 0;
}

.card-item .line:nth-child(2) {
  transform-origin: 100% 100%;
  height: 100%;
  width: 3px;
  bottom: 0;
  left: -1px;
}

.card-item .line:nth-child(5) {
  transform-origin: 100% 0;
  height: 100%;
  width: 3px;
  bottom: 0;
  right: -1px;
}

.card-item .line:nth-child(3) {
  transform-origin: 0 100%;
  height: 3px;
  width: 100%;
  top: -1px;
  left: -1px;
}

.card-item .line:nth-child(6) {
  transform-origin: 100% 0;
  height: 3px;
  width: 100%;
  bottom: 0;
  right: -1px;
}

.card-item:hover > .line {
  transform: scale(1);
  opacity: 0;
  transition: all 1s;
}

.card-item:hover > .line:nth-child(2),
.card-item:hover > .line:nth-child(5) {
  transition-delay: 0.3s;
}

.card-item:hover > .line:nth-child(3),
.card-item:hover > .line:nth-child(6) {
  transition-delay: 0.6s;
}

.card-item:hover {
  background: rgba(255, 255, 255, 0.03);
}

