  margin-bottom: 4px;
}

#faq-container .q-a-item.show {
  display: block;
}

#faq-container .q-a-item.is-active .question {
  border-radius: 8px 8px 0 0;
}

#faq-container .q-a-item.is-active .question span {
  border-bottom: 1px solid #D3D3D3;
}

#faq-container .q-a-item.is-active .question span:hover {
  border-bottom: 1px solid #d3d3d300;
}

#faq-container .q-a-item.is-active .question span::after {
  transform: rotate(180deg);
}

#faq-container .q-a-item .question {
  background: #FFF;
  text-align: left;
  line-height: 3.2rem;
  position: relative;
  border-radius: 8px;
}

#faq-container .q-a-item .question span {
  width: 100%;
  display: flex;
  align-items: center;
  column-gap: 2.2rem;
  padding: 1.6rem 0;
}

@media only screen and (max-width: 768px) {
  #faq-container .q-a-item .question span {
    column-gap: 0.8rem;
    padding: 1.2rem 0;
    line-height: 2.2rem;
  }
}

#faq-container .q-a-item .question span::after {
  content: '';
  flex-shrink: 0;
  background-image: url(../img/down-mark.svg);
  background-position: center center;
  background-size: 1.6rem 1.6rem;
  background-repeat: no-repeat;
  display: block;
  margin-left: auto;
  height: 3.2rem;
  width: 3.2rem;
  transform: rotate(0);
  transition: transform .2s ease-out;
}

#faq-container .q-a-item .question span::before {
  content: 'Q';
  flex-shrink: 0;
  color: #000;
  text-align: center;
  font-family: "DIN 2014";
  font-size: 3.2rem;
  font-style: normal;
  font-weight: bold;
  line-height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 4.8rem;
  width: 4.8rem;
  background: #FDD933;
  border: 2px solid #000;
  border-radius: 90px;
}

@media only screen and (max-width: 768px) {
  #faq-container .q-a-item .question span::before {
    width: 3.2rem;
    height: 3.2rem;
    font-size: 2.0rem;
  }
}

#faq-container .q-a-item .answer {
  padding: 0 3.2rem;
  display: none;
  color: #333;
  background: #FFF;
  line-height: 2.4rem;
  text-align: left;
  border-radius: 0 0 8px 8px;
}

@media only screen and (max-width: 768px) {
  #faq-container .q-a-item .answer {
    padding: 0 3.6rem 0 0.8rem;
  }
}

#faq-container .q-a-item .answer span {
  display: flex;
  align-items: center;
  column-gap: 2.2rem;
  padding: 1.6rem 0;
}

@media only screen and (max-width: 768px) {
  #faq-container .q-a-item .answer span {
    padding: 1.2rem 0;
    column-gap: 0.8rem;
  }
}

#faq-container .q-a-item .answer span::before {
  content: 'A';
  flex-shrink: 0;
  margin-bottom: auto;
  color: #FFF;
  text-align: center;
  font-family: "DIN 2014";
  font-size: 3.2rem;
  font-style: normal;
  font-weight: bold;
  line-height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 4.8rem;
  width: 4.8rem;
  background: #249E54;
  border-radius: 90px;
}

@media only screen and (max-width: 768px) {
  #faq-container .q-a-item .answer span::before {
    width: 3.2rem;
    height: 3.2rem;
    font-size: 2.0rem;
  }
}

footer {
  padding: 10rem 0 8.0rem 0;
  background: #D81718;
}

footer .container {
  position: relative;
}

footer .top-img {
  position: absolute;
  top: 10.9rem;
  right: 0;
  width: 40rem;
  opacity: 0.5;
}

footer .top-text {
  color: #FFF;
  margin-bottom: 6.8rem;
  display: flex;
  align-items: center;
}

footer .top-text .plus {
  font-size: 6.4rem;
  font-weight: 300;
  position: relative;
  margin-bottom: 0.5rem;
}

footer .top-text .plus::before {
  content: '';
  position: absolute;
  top: -2.2rem;
  left: 50%;
  transform: translate(-50%, 0);
  font-size: 1.2rem;
  width: 100%;
  white-space: nowrap;
}

footer .top-text .cross {
  font-size: 4.2rem;
  position: relative;
  color: #FFE109;
}

footer .top-text .cross::before {
  content: '';
  position: absolute;
  top: -2.4rem;
  left: 50%;
  transform: translate(-41%, 0);
  font-size: 1.2rem;
  width: 100%;
  white-space: nowrap;
  color: #FFF;
}

footer .middle-text {
  margin-bottom: 2.4rem;
  line-height: 5.2rem;
  color: #FFF;
}

footer .middle-text .spot {
  position: relative;
  color: #FFF;
}

footer .middle-text .spot::after {
  content: '';
  width: 1.6rem;
  height: 1.6rem;
  border-radius: 90px;
  background: #FFE109;
  position: absolute;
  top: -1.0rem;
  left: 50%;
  transform: translateX(-50%);
}

footer .bottom-text {
  line-height: 3.2rem;
  color: #FFF;
  margin-bottom: 4.8rem;
}

footer .bottom-wrapper {
  display: flex;
  column-gap: 4.8rem;
  align-items: flex-end;
  justify-content: space-between;
}

footer .bottom-wrapper .introduction-pannel {
  width: 48%;
  position: relative;
  padding: 3.2rem 4.8rem;
  border-radius: 10px;
  background: #FFF;
}

footer .bottom-wrapper .introduction-pannel .top-text {
  border-radius: 90px;
  padding: 0.8rem 2.0rem;
  text-align: center;
  color: #FFF;
  margin-bottom: 0.8rem;
  background: #D81718;
  display: flex;
  align-items: center;
  justify-content: center;
}

footer .bottom-wrapper .introduction-pannel .desc {
  line-height: 1.8rem;
  margin-bottom: 2.0rem;
}

footer .bottom-wrapper .introduction-pannel .left-right {
  display: flex;
  column-gap: 4.8rem;
  justify-content: space-between;
}

footer .bottom-wrapper .introduction-pannel .left-right .left-wrapper .item:not(:last-child) {
  margin-bottom: 1.6rem;
}

footer .bottom-wrapper .introduction-pannel .left-right .right .item:not(:last-child) {
  margin-bottom: 1.6rem;
}

footer .bottom-wrapper .introduction-pannel .left-right .title {
  color: #D81718;
  padding-bottom: 0.8rem;
  border-bottom: 2px solid #D81718;
  margin-bottom: 0.8rem;
}

footer .bottom-wrapper .introduction-pannel .left-right .desc {
  line-height: 2.6rem;
}

footer .bottom-wrapper .item-list {
  display: grid;
  grid-template-columns: repeat(2, minmax(0px, 1fr));
  column-gap: 2.4rem;
  row-gap: 2.4rem;
}

footer .bottom-wrapper .item-list li {
  line-height: 2.4rem;
  color: #FFF;
}

footer .bottom-wrapper .item-list li .head-wrapper {
  display: flex;
  column-gap: 0.8rem;
  align-items: center;
  color: #FFF;
  padding-bottom: 0.8rem;
  border-bottom: 1px solid #FFF;
  margin-bottom: 1.6rem;
}

footer .bottom-wrapper .item-list li .head-wrapper img {
  width: 3.2rem;
  height: 3.2rem;
}
